import { useState } from 'react';
import { motion } from 'framer-motion';
import { Button, TextField, Radio, RadioGroup, FormControlLabel } from '@mui/material';
import { toast } from 'react-hot-toast';  // Импортируем toast

export default function CoinFlipGame() {
  const [bet, setBet] = useState(1);
  const [side, setSide] = useState('орёл');
  const [balance, setBalance] = useState(100);
  const [isFlipping, setIsFlipping] = useState(false);
  const [result, setResult] = useState<'орёл' | 'решка' | null>(null);

  const flipCoin = () => {
    if (bet > balance) {
      toast.error('Your bet cannot exceed your current balance.');  // Используем toast для ошибки
      return;
    }

    setIsFlipping(true);
    setResult(null);

    setTimeout(() => {
      const flipResult = Math.random() < 0.5 ? 'орёл' : 'решка';
      setResult(flipResult);
      setIsFlipping(false);

      if (flipResult === side) {
        setBalance(balance + bet);
        toast.success(`You've won $${bet}. Your new balance is $${balance + bet}.`);  // Используем toast для успеха
      } else {
        setBalance(balance - bet);
        toast.error(`You've lost $${bet}. Your new balance is $${balance - bet}.`);  // Используем toast для ошибки
      }
    }, 2000);
  };

  return (
      <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-r from-purple-400 via-pink-500 to-red-500">
        <div className="p-8 bg-white rounded-lg shadow-xl w-full max-w-md">
          <h1 className="text-3xl font-bold text-center mb-6">Russian Ruble Flip Game</h1>

          <div className="mb-6 text-center">
            <motion.div
                className="w-32 h-32 rounded-full bg-yellow-400 mx-auto flex items-center justify-center"
                animate={{
                  rotateY: isFlipping ? 1800 : 0,
                  scale: isFlipping ? 1.2 : 1,
                }}
                transition={{ duration: 2 }}
            >
              {result && <div className="text-2xl font-bold">{result}</div>}
            </motion.div>
          </div>

          <div className="space-y-4">
            <div>
              <TextField
                  label="Your Bet ($)"
                  type="number"
                  value={bet}
                  onChange={(e) => setBet(Math.max(1, parseInt(e.target.value)))}
                  fullWidth
                  inputProps={{ min: 1, max: balance }}
              />
            </div>

            <RadioGroup value={side} onChange={(e) => setSide(e.target.value)}>
              <FormControlLabel value="орёл" control={<Radio />} label="Орёл (Eagle)" />
              <FormControlLabel value="решка" control={<Radio />} label="Решка (Tails)" />
            </RadioGroup>

            <Button variant="contained" onClick={flipCoin} disabled={isFlipping} fullWidth>
              {isFlipping ? 'Flipping...' : 'Flip Coin'}
            </Button>

            <div className="text-center text-xl font-semibold">Balance: ${balance}</div>
          </div>
        </div>
      </div>
  );
}
